<template>
  <div class="wrapper" :style="wrapperStyle" style="padding-bottom: 70px;">
    <!-- <parallax class="page-header header-filter" :style="headerStyle"> -->
    <parallax class="page-header header-filter head-head" style="height: 0px">
    </parallax>

    <div class="main main-raised" style="padding-bottom: 0px;">
      <img
        :src="vector"
        :class="{ 'responsive-image': responsive }"
        class="img-fluid"
        style="width: 100%; margin-top: -7px"
      />
      <div class="section section-basic" style="padding-bottom: 0px">
        <div class="container" style="padding: 0px 20px">
          <div style="margin-top: 10px; font-size: 12px; color: #0F0F5F">
            <strong>{{
              infoData.updated_at | moment("dddd, D MMMM YYYY")
            }}</strong>
          </div>
          <div class="title page">
            {{ infoData.title }}
          </div>

          <div class="container" style="padding: 10px;">
            <div class="md-layout md-alignment-center-center">
              <img
                :src="infoData.image"
                :class="{ 'responsive-image': responsive }"
                class="img-raised img-fluid"
                style="border-radius: 20px; width: auto; max-height: 500px;"
              />
            </div>
          </div>
          <div class="posted" v-html="infoData.text"></div>
        </div>
      </div>
      <img
        :src="vector1"
        :class="{ 'responsive-image': responsive }"
        class="img-fluid"
        style="width: 100%; margin-bottom: -6px"
      />
    </div>
    <go-top
      bg-color="#fae019"
      fg-color="#0f0f5f"
      box-shadow="0 0 0 transparent"
    >
    </go-top>
  </div>
</template>

<script>
// import BasicElements from "./components/BasicElementsSection";
import { mapState, mapActions } from "vuex";
import GoTop from "@inotom/vue-go-top";

export default {
  components: {
    GoTop
  },
  data() {
    return {};
  },
  name: "profil",
  bodyClass: "index-page",
  props: {
    profil: {
      type: String,
      default: require("@/assets/img/examples/pixeltrue-success.png")
    },
    info: {
      type: String,
      default: require("@/assets/img/examples/image1.jpeg")
    },
    vector: {
      type: String,
      default: require("@/assets/img/examples/vector.png")
    },
    vector1: {
      type: String,
      default: require("@/assets/img/examples/vector1.png")
    },
    image: {
      type: String,
      default: require("@/assets/img/logo/image.png")
    }
  },
  computed: {
    ...mapState("info", ["infoData", "postList"]),
    headerStyle() {
      return {
        // backgroundImage: `url(${this.homeGalleryData.sampul_image})`
      };
    },
    wrapperStyle() {
      return {
        // backgroundImage: `url(${this.temaData.tema_image})`
      };
    }
  },
  methods: {
    ...mapActions("info", ["getInfo", "getPostList"]),
    async onFetchData() {
      await this.getInfo({ id: this.$route.params.id });
      await this.getPostList();
    },
    onResponsiveInverted() {
      if (window.innerWidth < 600) {
        this.responsive = true;
      } else {
        this.responsive = false;
      }
    },
    classicModalHide() {
      this.classicModal = false;
    },
    handleScroll: function() {
      if (this.scTimer) return;
      this.scTimer = setTimeout(() => {
        this.scY = window.scrollY;
        clearTimeout(this.scTimer);
        this.scTimer = 0;
      }, 100);
    },
    toTop: function() {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    }
  },
  async mounted() {
    this.onResponsiveInverted();
    await this.onFetchData();
    // console.log(process.env.VUE_APP_BASE_URL);
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("resize", this.onResponsiveInverted);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResponsiveInverted);
  }
};
</script>
<style lang="scss">
.section-download {
  .md-button + .md-button {
    margin-left: 5px;
  }
}

@media all and (min-width: 991px) {
  .btn-container {
    display: flex;
  }
}
</style>
<style lang="css">
.posted {
  font-size: 1rem;
  text-align: justify;
  color: #0f0f5f;
  margin-top: 30px;
}

.post {
  text-align: justify;
  color: #0f0f5f;
  margin-bottom: -8px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.page {
  margin-top: 0px !important;
  margin-bottom: 50px;
  font-size: 40px;
  line-height: 50px;
  text-align: justify;
}

@media screen and (max-width: 700px) {
  .page {
    margin-top: 0px !important;
    margin-bottom: 50px;
    font-size: 27px;
    line-height: 32px;
    text-align: justify;
  }
}
</style>
